@import "../../styles/mixins.scss";

.minting_info_container {
    width: 627px;
    overflow: hidden;
    float: left;

    @include breakpoint(md) {
        float: initial;
        margin: 0 auto 20px auto;
    }

    .info_block {
        width: 305px;
        height: 133px;
        background: #FFFFFF;
        border: 2px solid #DEDEDE;
        border-radius: 20px;
        margin: 0 17px 18px 0;
        text-align: center;
        float: left;
        padding-top: 30px;

        .info_title {
            font-family: 'Exo 2';
            font-style: normal;
            font-weight: 700;
            font-size: 20px;
            /* identical to box height, or 200% */

            color: #BFBFBF;
            margin-bottom: 10px;
        }

        .info_contents {
            font-family: 'Exo 2';
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            color: #2A355E;
        }
    }

    .info_block:nth-child(2n) {
        margin-right: 0;
    }

    .progress_block {
        width: 627px;
        height: 92px;
        background: #FFFFFF;
        border: 2px solid #DEDEDE;
        border-radius: 20px;
        padding: 20px 16px 0 16px;
        overflow: hidden;
        margin-bottom: 18px;

        .progress_num_block {
            overflow: hidden;
            margin-bottom: 7px;


            .progress_num_text {
                float: left;
                width: 33%;
                font-family: 'Exo 2';
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                color: #2A355E;
                text-align: left;
            }

            .progress_num_text:nth-child(2) {
                text-align: center;
            }

            .progress_num_text:nth-child(3) {
                text-align: right;
            }
        }

        .progress_bar {
            background: #FFFFFF;
            border: 1px solid #DEDEDE;
            border-radius: 6px;
            width: 596px;
            height: 12px;
            -webkit-appearance: none;
        }

        .progress_bar::-webkit-progress-bar {
            background: #fff;
            border-radius: 7px;
        }

        .progress_bar::-webkit-progress-value {
            border-radius: 7px;
            background: #6B1FFE;
        }
    }

    .control_container {
        width: 627px;
        height: 187px;
        background: #FFFFFF;
        border: 2px solid #DEDEDE;
        border-radius: 20px;
        text-align: center;


        .amount_block {
            margin: 18px auto 8px auto;
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            /* or 167% */

            align-items: center;
            text-align: center;
            color: #2A355E;
        }

        .control_block {
            width: 411px;
            margin: 0 auto;
            overflow: hidden;

            .minus_btn {
                margin-top: 5px;
                width: 28px;
                height: 28px;
                float: left;
                border: 0;
                background: url('../../assets/images/minus.png') no-repeat;
                background-size: cover;
                cursor: pointer;
            }

            .minus_btn:hover {
                filter: brightness(110%);
            }

            .minus_btn:active {
                filter: brightness(90%);
            }

            .count_block {
                width: 355px;
                text-align: center;
                float: left;
                font-style: normal;
                font-weight: 500;
                font-size: 32px;
                /* or 125% */

                text-align: center;
                color: #2A355E;
            }

            .plus_btn {
                margin-top: 5px;
                width: 28px;
                height: 28px;
                float: right;
                border: 0;
                background: url('../../assets/images/plus.png') no-repeat;
                background-size: cover;
                cursor: pointer;
            }

            .plus_btn:hover {
                filter: brightness(110%);
            }

            .plus_btn:active {
                filter: brightness(90%);
            }
        }

        .button_block {
            margin: 8px auto 0 auto;
            width: 411px;
            height: 67px;
            background: linear-gradient(90deg, #a889ff -6.65%, #38fec3 159.12%);
            backdrop-filter: blur(2px);
            /* Note: backdrop-filter has minimal browser support */

            border-radius: 10px;
            text-align: center;
            font-style: normal;
            font-weight: 700;
            font-size: 24px;
            /* or 167% */

            padding-top: 5px;
            color: #FFFFFF;
            border: 0;
            cursor: pointer;
        }

        .button_block:hover {
            background: linear-gradient(90deg, #b899ff -6.65%, #48fed3 159.12%);
        }

        .button_block:active {
            background: linear-gradient(90deg, #9879ef -6.65%, #28eeb3 159.12%);
        }
    }
}


.popup_block {
    width: 538px;
    height: 400px;
    background: #FFFFFF;
    border-radius: 20px;

    .popup_contents_block {
        padding-top: 68px;

        .popup_icon_success {
            width: 70px;
            height: 70px;
            background: url('../../assets/images/icon_Congratulation.png') no-repeat;
            background-size: cover;
            margin: 0 auto 20px auto;
        }

        .popup_icon_failed {
            width: 70px;
            height: 70px;
            background: url('../../assets/images/icon_sad.png') no-repeat;
            background-size: cover;
            margin: 0 auto 20px auto;
        }

        .popup_title {
            font-style: normal;
            font-weight: 500;
            font-size: 40px;
            line-height: 40px;
            /* or 100% */

            color: #000000;
            text-align: center;
            margin-bottom: 20px;
        }

        .popup_contents {
            margin: 0 10px;
            font-style: normal;
            height: 87px;
            font-weight: 400;
            font-size: 24px;
            /* or 167% */

            text-align: center;
            color: #636363;
        }
    }

    .close_popup {
        width: 466px;
        height: 76px;
        background: linear-gradient(90deg, #a889ff -6.65%, #38fec3 159.12%);
        border-radius: 10px;
        margin: 0 auto;
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 40px;
        /* or 167% */

        text-align: center;
        color: #FFFFFF;
        padding-top: 20px;
        cursor: pointer;
    }

    .close_popup:hover {
        background: linear-gradient(90deg, #b899ff -6.65%, #48fed3 159.12%);
    }

    .close_popup:active {
        background: linear-gradient(90deg, #9879ef -6.65%, #28eeb3 159.12%);
    }
}