@import "../../styles/mixins.scss";

.banner {

    .bg_blur {
        background: url('../../assets/images/BG_blur effect.jpg') no-repeat;
        position: fixed;
        width: 100vw;
        height: 100vh;
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-size: cover;
        z-index: -1;

        @include breakpoint(md) {
            width: 100%;
            height: 100%;
        }
    }

    width: 100%;

    .top_container {
        width: 100%;
        height: 138px;
        padding: 35px 78px 0 62px;
        overflow: hidden;

        .logo {
            margin-top: 19px;
            background: url('../../assets/images/logo_01.png') no-repeat;
            width: 260px;
            height: 43px;
            float: left;
        }
    }

    .middle_container {
        width: 1164px;
        margin: 44px auto 7px auto;
        overflow: hidden;

        @include breakpoint(md) {
            width: 100%;
        }

        .nft_info_container {
            width: 524px;
            margin-right: 13px;
            background: #FFFFFF;
            border: 2px solid #DEDEDE;
            border-radius: 20px;
            float: left;

            @include breakpoint(md) {
                float: initial;
                width: 627px;
                margin: 0 auto 20px auto;
            }

            .nft_image {
                margin: 42px auto 39px auto;
                width: 375px;
                height: 375px;
                border-radius: 20px;
                background: url('../../assets/images/nft.jpg') no-repeat;
                background-size: cover;
            }

            .nft_contents_container {
                width: 425px;
                height: 260px;
                margin: 0 38px 30px 61px;
                overflow-y: scroll;
                position: sticky;
                top: 0;
                padding-right: 20px;

                @include breakpoint(md) {
                    width: 525px;
                }

                .nft_contents_title {
                    display: block;
                    font-family: 'Exo 2';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 22px;
                    /* or 75% */
                    color: #000000;
                }

                .nft_contents_head {
                    display: block;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 20px;
                    /* or 109% */

                    color: #000000;
                    margin-top: 16px;
                }

                .nft_contents_text {
                    display: block;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 18px;
                    /* or 109% */
                    color: #000000;
                }

                .nft_contents_text_indent {
                    display: block;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 11px;
                    line-height: 12px;
                    /* or 109% */
                    color: #000000;
                    margin-left: 7px; /* 1 */
                    text-indent: -7px; /* 2 */
                }
            }

            .nft_contents_container::-webkit-scrollbar {
                width: 12px;
            }
            /* Track */
            .nft_contents_container::-webkit-scrollbar-track {
                box-shadow: inset 0 0 2px grey;
                border-radius: 10px;
            }
            /* Handle */
            .nft_contents_container::-webkit-scrollbar-thumb {
                background: #B8B8B8;
                border-radius: 10px;
            }
            /* Handle on hover */
            .nft_contents_container::-webkit-scrollbar-thumb:hover {
                background: #999999;
            }

            .benefit_container {
                margin: 0 auto;
                width: 304px;
                height: 50px;
                margin-bottom: 5px;
            }
        }
    }

    .bottom_container {
        margin: 40px auto 0 auto;
        background: url('../../assets/images/logo_02.png') no-repeat;
        width: 200px;
        height: 109px;
        background-size: cover;
    }
}

.settings {
    @include position($position: absolute, $top: 25px, $right: 0px);
}
